import SelectTime from "../../plugin/components/SelectTime";
import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import PermissionButton from "../../plugin/components/PermissionButton";
import UserHandler from "../../script/handlers/UserHandler";
import InputArea from "../../plugin/components/InputArea";
import InfoTableItem from "../../plugin/components/InfoTableItem";

export default {
  name: "GameSortConf",
  extends: BaseIframe,
  components: {
    InfoTableItem,
    InputArea,
    SelectTime,
    InfoTable,
    PermissionButton
  },
  data() {
    return {
      value: true,
      labelPosition: "left", //左对齐
      dialogFormVisible: false,
      searchChannel: "", //table页面渠道列表
      funOpts: [
        { value: "1", label: "留空" },
        { value: "2", label: "活动" },
        { value: "3", label: "邮件" },
        { value: "4", label: "排行榜" },
        { value: "5", label: "设置" },
        { value: "6", label: "保险箱" },
        { value: "7", label: "广播" },
        { value: "8", label: "财神" }
      ],
      gameOpts: [],
      tableData: [],
      form: {
        checkList: [],
        function: "1",
        status: "normal",
        corner_remark: "recommend",
        leader: "no",
        agent: 100,
        nickname: "",
        password: "",
        money_password: "",
        phone: "",
        user_type: "1"
      },
      channelOpts: [],
      allChannelOpts: []
    };
  },
  methods: {
    sendDataToServer() {
      let data = {
        type_id: 4
      };
      this.$http.post("v1/backend/lobby/server_config_two", data).then(res => {
        console.log(res);
        if (res.data.code === 1) {
          this.$message({
            type: "success",
            message: res.data.msg
          });
        }
      });
    },
    getGameSortList() {
      this.$http
        .get("v1/backend/lobby/game_sort", {
          params: {
            page: 1,
            limit: 10
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.tableData = res.data.data;
          }
        });
    },
    getGameList() {
      this.$http
        .get("/v1/backend/lobby/game_list", {
          params: {
            page: this.currentPage,
            limit: this.pagesize,
            parent_id: this.form.parent_id
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.gameOpts = res.data.data.slice(1);
          }
        });
    },
    //获取新增页面的渠道列表
    getAddChannelList() {
      let data = {
        type_id: 2,
        add_id: 1
      };
      this.$http.post("v1/backend/no_channel", data).then(res => {
        console.log(res);
        if (res.data.code === 1) {
          this.channelOpts = res.data.data;
        }
      });
    },
    //获取列表页的所有渠道
    getAllChannelList() {
      let data = {
        type_id: 2,
        add_id: 2
      };
      this.$http.post("v1/backend/no_channel", data).then(res => {
        console.log(res);
        if (res.data.code === 1) {
          this.allChannelOpts = res.data.data;
        }
      });
    },
    openAddDialog() {
      this.dialogFormVisible = true;
      this.getAddChannelList();
      this.getGameList();
    }
  },
  mounted() {
    this.getGameSortList();
    this.getAllChannelList();
  }
};



